.burger {
  width: 100vw;
  margin: auto;
  height: 150px;
  overflow: auto;
  text-align: center;
  font-weight: bold;
  font-size: 1.2rem;
}

@media(min-width: 500px) and (min-height: 400px) {
  .burger {
    width: 350px;
    height: 200px;
  }
}

@media(min-width: 500px) and (min-height: 401px) {
  .burger {
    width: 450px;
    height: 300px;
  }
}

@media(min-width: 1000px) and (min-height: 700px) {
  .burger {
    width: 30vw;
    height: 500px;
  }
}